.icon-wrapper {
    position: relative;
    padding: 0px 30px;
  }
  
  .icon-wrapper .anticon {
    position: absolute;
    top: -2px;
    width: 16px;
    height: 16px;
    line-height: 1;
    font-size: 16px;
    color: rgba(0, 0, 0, 0.25);
  }
  
  .icon-wrapper .anticon:first-child {
    left: 0;
  }
  
  .icon-wrapper .anticon:last-child {
    right: 0;
  }